@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header {
    background: #FC354C;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #0ABFBC, #FC354C); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    padding: 4em;
    color: #ececec;
    text-align: center;
}

header img {
    margin-bottom: 2em;
    width: 12em;
    height: auto;
    transition: .8s ease-in-out;
}

header img:hover {
    transform: rotate(360deg) scale(1.5);
}

header h1 {
    font-family: 'Pacifico', cursive;
}
footer {
    background: #1a1a1a;
    color: #9a9a9a;
    text-align: center;
    padding: 2em 4em;
}
.list-container {
    margin: 2em auto;
}


h2.list-header {
    font-family: 'Balsamiq Sans', cursive;
    font-weight: 700;
    font-size: 2.5em;
    background-color: #6c547c;
    padding: 1em;
    color: #ececec;
    border-radius: 8px;
}

.list-input {
    width: 100%;
    border-radius: 8px;
    font-size: 1.2em;
    margin-bottom: 1em;
    margin-top: 0.5em;
    padding: 1em;
}

.submit-button {
    font-size: 1.2em;
    margin-top: 0.5em;
    padding: 1em;
    background-color: #6c547c;
}
.list-item {
    background-color: #00b0b4;
    border-radius: 8px;
    padding: 1em;
    color: #1a1a1a;
    font-family: 'Balsamiq Sans', cursive;
    font-size: 1.4em;
    margin-bottom: 0.5em;
}

.list-item:hover {
    transform: scale(1.02)
}

.list-item input[type=checkbox] {
    transform: scale(2.5);
    margin-left: 0em;
}

